import {useDispatch} from 'react-redux'
import {useLocation} from 'react-router-dom'
import {useEffect} from 'react'
import {
  StyledAnchor,
  StyledBDLPage,
  StyledHead,
  StyledParagraph,
  StyledSection,
  StyledSectionHeader,
  StyledTableBox,
  StyledTbody,
  StyledTd,
  StyledTh,
  tr,
} from './styled'
import {setTeritorialUnitBegin} from '../teritorialUnit/teritorialUnitSlice'
import {setManyVariablesBegin} from '../manyVariables/manyVariablesSlice'
import {setSelectedMapBegin} from '../maps/mapsSlice'

export default function LocalDataBank() {
  const dispatch = useDispatch()
  const {pathname} = useLocation()

  useEffect(() => {
    pathname === '/bank-danych-lokalnych'
      ? dispatch(setTeritorialUnitBegin()) &&
        dispatch(setManyVariablesBegin()) &&
        dispatch(setSelectedMapBegin())
      : ''
  }, [])

  return (
    <StyledBDLPage>
      <StyledHead>Bank danych lokalnych</StyledHead>
      <StyledSection>
        <StyledSectionHeader>
          BANK DANYCH LOKALNYCH - najwiksza w Polsce baza danych o gospodarce,
          spoeczestwie i rodowisku.
        </StyledSectionHeader>
        <StyledParagraph>
          Bank Danych Lokalnych oferuje ponad 40 tys. cech statystycznych
          pogrupowanych tematycznie. Pierwsze dane pochodz z 1995 roku.
        </StyledParagraph>
        <ul>
          <li>Dane i wskaniki opisuj powiaty, wojewdztwa i Polsk. </li>
          <li>
            Zasoby informacyjne (statystyki roczne i krtkookresowe) s na
            bieco uzupeniane oraz aktualizowane.
          </li>
        </ul>
        <StyledParagraph>
          Aplikacja API pozwala przeglda i pobiera do dalszego przetwarzania
          peny zakres danych znajdujcych si w Banku. API do Banku Danych
          Lokalnych zrealizowano w ramach partnerskiego projektu Otwarte dane -
          dostp, standard, edukacja, ktrego Liderem jest Ministerstwo
          Cyfryzacji. Projekt jest wspfinansowany ze rodkw Unii Europejskiej
          z Programu Operacyjnego Polska Cyfrowa.
        </StyledParagraph>
        <StyledParagraph>
          Aplikacja pozwala na przegldanie danych w dwch trybach:
        </StyledParagraph>
        <ul>
          <li>jedna jednostka terytorialna dla wielu dziedzin tematycznych </li>
          <li>jedna dziedzina tematyczna dla wielu jednostek terytorialnych</li>
        </ul>
        <StyledParagraph>
          Podczas wyboru jednostki terytorialnej w zakadce Dziedziny Tematyczne
          wyniki bd wywietlone dla wszystkich jednostek terytorialnych
          niszego szczebla.
        </StyledParagraph>
        <StyledParagraph>
          Wyniki mog by wywietlone w postaci wykresu lub tabeli.
        </StyledParagraph>
      </StyledSection>
      <StyledSection>
        <StyledSectionHeader>Limity zapyta</StyledSectionHeader>
        <StyledParagraph>
          Ze wzgldw bezpieczestwa API na ilo da naoone s limity
          ilociowe.
        </StyledParagraph>
        <StyledTableBox>
          <StyledTbody>
            <tr>
              <StyledTh>Okres</StyledTh>
              <StyledTh>Ilo zapyta</StyledTh>
            </tr>
            <tr>
              <StyledTd>1s</StyledTd>
              <StyledTd>5</StyledTd>
            </tr>
            <tr>
              <StyledTd>15m</StyledTd>
              <StyledTd>100</StyledTd>
            </tr>
            <tr>
              <StyledTd>12h</StyledTd>
              <StyledTd>1&nbsp;000</StyledTd>
            </tr>
            <tr>
              <StyledTd>7d</StyledTd>
              <StyledTd>10&nbsp;000</StyledTd>
            </tr>
          </StyledTbody>
        </StyledTableBox>
      </StyledSection>
      <StyledSection>
        <StyledSectionHeader>Licencja</StyledSectionHeader>
        <StyledParagraph>
          Dane s moliwe do wykorzystywania w oparciu o licencj midzynarodow
          Creative Commons by 4.0 - Uznanie autorstwa
        </StyledParagraph>
        <StyledAnchor href="https://creativecommons.org/licenses/by/4.0/legalcode.pl">
          https://creativecommons.org/licenses/by/4.0/legalcode.pl
        </StyledAnchor>
        <StyledParagraph>
          Wacicielem aplikacji API udostpniajcej dane jest Gwny Urzd
          Statystyczny
        </StyledParagraph>
        <StyledAnchor href="https://api.stat.gov.pl/Home/BdlApi">
          https://api.stat.gov.pl/Home/BdlApi
        </StyledAnchor>
      </StyledSection>
    </StyledBDLPage>
  )
}
